<template>
    <ot-section v-bind="$attrs" :theme="$otTheme" label="Tabs 标签页 + 自定义标签" section>
        <ot-row-group :theme="$otTheme" :code="code">
            <ot-tabs :class="$style.tabs" :theme="$otTheme" :list="tabs" :select="selectTab" @action="handleAction">
                <span slot="title" slot-scope="{ item }">
                    <ot-icon icon="apple">
                        <span>{{ item.title }}</span>
                    </ot-icon>
                </span>
                <span>{{ selectTab && selectTab.title }}</span>
            </ot-tabs>
        </ot-row-group>
    </ot-section>
</template>

<script>
export default {
    data() {
        return {
            selectTab: null,
            tabs: [{
                title: 'OT',
                name: 'OT',
            }],
            code: decodeURIComponent(`%3Ctemplate%3E%0A%20%20%20%20%3Cot-section%20label%3D%22Tabs%20%E6%A0%87%E7%AD%BE%E9%A1%B5%20%2B%20%E8%87%AA%E5%AE%9A%E4%B9%89%E6%A0%87%E7%AD%BE%22%20section%3E%0A%20%20%20%20%20%20%20%20%3Cot-row-group%3E%0A%20%20%20%20%20%20%20%20%20%20%20%20%3Cot-tabs%20%3Aclass%3D%22%24style.tabs%22%20%3Alist%3D%22tabs%22%20%3Aselect%3D%22selectTab%22%20%40action%3D%22handleAction%22%3E%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%3Cspan%20slot%3D%22title%22%20slot-scope%3D%22%7B%20item%20%7D%22%3E%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%3Cot-icon%20icon%3D%22apple%22%3E%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%3Cspan%3E%7B%7B%20item.title%20%7D%7D%3C%2Fspan%3E%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%3C%2Fot-icon%3E%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%3C%2Fspan%3E%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%3Cspan%3E%7B%7B%20selectTab%20%26%26%20selectTab.title%20%7D%7D%3C%2Fspan%3E%0A%20%20%20%20%20%20%20%20%20%20%20%20%3C%2Fot-tabs%3E%0A%20%20%20%20%20%20%20%20%3C%2Fot-row-group%3E%0A%20%20%20%20%3C%2Fot-section%3E%0A%3C%2Ftemplate%3E%0A%0A%3Cscript%3E%0Aexport%20default%20%7B%0A%20%20%20%20data()%20%7B%0A%20%20%20%20%20%20%20%20return%20%7B%0A%20%20%20%20%20%20%20%20%20%20%20%20selectTab%3A%20null%2C%0A%20%20%20%20%20%20%20%20%20%20%20%20tabs%3A%20%5B%7B%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20title%3A%20'OT'%2C%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20name%3A%20'OT'%2C%0A%20%20%20%20%20%20%20%20%20%20%20%20%7D%5D%2C%0A%20%20%20%20%20%20%20%20%7D%3B%0A%20%20%20%20%7D%2C%0A%20%20%20%20methods%3A%20%7B%0A%20%20%20%20%20%20%20%20initTabs()%20%7B%0A%20%20%20%20%20%20%20%20%20%20%20%20for%20(let%20i%20%3D%200%3B%20i%20%3C%209%3B%20i%2B%2B)%20%7B%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20this.tabs.push(%7B%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20title%3A%20%60Tabs%20%24%7Bi%7D%60%2C%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20name%3A%20i%20%2B%20''%2C%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%7D)%3B%0A%20%20%20%20%20%20%20%20%20%20%20%20%7D%0A%20%20%20%20%20%20%20%20%20%20%20%20this.selectTab%20%3D%20this.tabs%5B0%5D%3B%0A%20%20%20%20%20%20%20%20%7D%2C%0A%20%20%20%20%20%20%20%20handleAction(action%2C%20tab)%20%7B%0A%20%20%20%20%20%20%20%20%20%20%20%20console.log(action%2C%20tab)%3B%0A%20%20%20%20%20%20%20%20%20%20%20%20switch%20(action)%20%7B%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20case%20'active'%3A%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20this.selectTab%20%3D%20tab%3B%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20break%3B%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20case%20'close'%3A%20%7B%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20const%20tabs%20%3D%20this.tabs%3B%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20const%20targetName%20%3D%20tab.name%3B%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20let%20activePluginsTab%20%3D%20this.selectTab%3B%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20if%20(activePluginsTab.name%20%3D%3D%3D%20targetName)%20%7B%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20tabs.forEach((tab%2C%20index)%20%3D%3E%20%7B%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20if%20(tab.name%20%3D%3D%3D%20targetName)%20%7B%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20const%20nextTab%20%3D%20tabs%5Bindex%20%2B%201%5D%20%7C%7C%20tabs%5Bindex%20-%201%5D%3B%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20if%20(nextTab)%20%7B%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20activePluginsTab%20%3D%20nextTab%3B%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%7D%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%7D%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%7D)%3B%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%7D%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20this.selectTab%20%3D%20activePluginsTab%3B%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20this.tabs%20%3D%20tabs.filter(tab%20%3D%3E%20tab.name%20!%3D%3D%20targetName)%3B%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%7D%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20break%3B%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20default%3A%0A%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20%20break%3B%0A%20%20%20%20%20%20%20%20%20%20%20%20%7D%0A%20%20%20%20%20%20%20%20%7D%2C%0A%20%20%20%20%7D%2C%0A%20%20%20%20created()%20%7B%0A%20%20%20%20%20%20%20%20this.initTabs()%3B%0A%20%20%20%20%7D%2C%0A%7D%3B%0A%3C%2Fscript%3E%0A%0A%3Cstyle%20lang%3D%22scss%22%20module%3E%0A.tabs%20%7B%0A%20%20%20%20margin%3A%200%20!important%3B%0A%20%20%20%20padding%3A%205px%2020px%3B%0A%7D%0A%3C%2Fstyle%3E`),
        };
    },
    methods: {
        initTabs() {
            for (let i = 0; i < 9; i++) {
                this.tabs.push({
                    title: `Tabs ${i}`,
                    name: i + '',
                });
            }
            this.selectTab = this.tabs[0];
        },
        handleAction(action, tab) {
            console.log(action, tab);
            switch (action) {
                case 'active':
                    this.selectTab = tab;
                    break;
                case 'close': {
                    const tabs = this.tabs;
                    const targetName = tab.name;
                    let activePluginsTab = this.selectTab;
                    if (activePluginsTab.name === targetName) {
                        tabs.forEach((tab, index) => {
                            if (tab.name === targetName) {
                                const nextTab = tabs[index + 1] || tabs[index - 1];
                                if (nextTab) {
                                    activePluginsTab = nextTab;
                                }
                            }
                        });
                    }
                    this.selectTab = activePluginsTab;
                    this.tabs = tabs.filter(tab => tab.name !== targetName);
                }
                    break;
                default:
                    break;
            }
        },
    },
    created() {
        this.initTabs();
    },
};
</script>

<style lang="scss" module>
.tabs {
    margin: 0 !important;
    padding: 5px 20px;
}
</style>

